.App {
  text-align: center;
}

.content {
  margin-top: 60px;
}

.nav {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 60px;
  background: black;
}

.nav-flex {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 1.2rem;
}

.nav-class {
  color: white;
  height: 100%;
  position: relative;
  display: inline-block;
  width: 170px;
  cursor: pointer;
}

.nav-dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  width: 170px;
  font-size: 1rem;
}

.nav-dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.nav-dropdown-content a:hover {background-color: #ddd;}

.nav-class:hover .nav-dropdown-content {display: block;}

.nav-class:hover p {color: red;}

.task-title {
  font-size: 1.5rem;
}